.product {
    padding-bottom: 2rem;

    &__text {
        flex: 0 0 40%;
    }

    &__link {
        flex: 0 0 60%;
        height: 60%;
        padding: 1rem;
        position: relative;
    }

    a {
        font-size: 1.15rem;
        color: black;
        text-decoration: none;
    }

    a:hover {
        text-decoration: underline;
        color: blue;
    }

    &__description {
        font-size: 0.7rem;
    }

    &:hover {
        box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
    }

    &__add-to-cart {
        position: absolute;
        bottom: 0.8rem;
        width: 60%;
        left: 50%;
        opacity: 1;
        visibility: hidden;
        transform: translateX(-50%);
        transition: all 0.2s;
        padding: 3px 12px;
    }

    &:hover &__add-to-cart {
        visibility: visible;
        opacity: 1;
        // background-color: #f8f9fa;
    }

    &:hover .owl-dots {
        display: inline-block;
    }
}

@media only screen and (max-width: 768px) {
    .product {
        height: auto !important;
        &__add-to-cart {
            visibility: visible;
            opacity: 1;
        }

        & .owl-dots {
            display: inline-block;
        }
    }
}
